.cncium__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}


.cncium__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.cncium__navbar-links_logo {
    margin-right: 2rem;
}

.cncium__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.cncium__navbar-links_container {
    display: flex;
    flex-direction: row;

}

.cncium__navbar-contact {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.cncium__navbar-links_container p,
.cncium__navbar-contact p,
.cncium__navbar-menu p {
    color: #FFF; 
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px; 
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.cncium__navbar-contact button,
.cncium__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #FFF;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    cursor: pointer;
}


.cncium__navbar-menu {
    margin: 1rem;
    display: none;
    position: relative;
}

.cncium__navbar-menu svg {
    cursor: pointer;
}

.cncium__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.cncium__navbar-menu_container p {
    margin: 1rem 0;
}

.cncium__navbar-menu_container-links-contact {
    display: none;
}

@media screen and (max-width: 1050px) {
    .cncium__navbar-links_container {
        display: none;
    }
    .cncium__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .cncium__navbar {
        padding: 2rem 4rem;
    }
    .cncium__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 550px) {
    .cncium__navbar {
        padding: 2rem;
        margin-bottom: -3rem;
    }
    .cncium__navbar-menu {
        display: flex;
    }
    .cncium__navbar-contact {
        display: none;
    }
    .cncium__navbar-menu_container {
        top:20px;
    }
    .cncium__navbar-menu_container-links-contact {
        display: block;
    }
}

